.edu-ind-pro-elearning {
  font-family: "Poppins", sans-serif !important;
  font-weight: 500;
  color: #3680f7;
  font-size: 20px;
  line-height: 1;
}

#eduIndProduct {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
}

.eduIndProductGrid_Container {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1.5fr;
  width: 100%;
  position: relative;
  gap: 0.5rem;
}

.eduGrid_Item_1 {
  background-image: url("../../assets/images/education-industries/edu1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 5px solid;
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
}
.eduGrid_innerItem {
  background-image: linear-gradient(to right, #000000c2 5%, #000000c2 50%);
  height: 100%;
}
.eduProduct_description {
  margin: 0px;
  color: white;
  padding-bottom: 2rem;
  font-size: 17px;
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  line-height: 1.5;
}

.eduGrid_innerDescription_btn_sec {
  width: 60%;
  margin: auto;
  padding: 7rem 0;
}

.eduGrid_Readusecases_btn {
  background: none;
  border: none;
  color: #fff;
  padding: 0px;
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  line-height: 1.2;
  font-size: 17px;
}

.eduGrid_Item_2 {
  background-image: url("../../assets/images/education-industries/edu2.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 5px solid;
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
}

.internshipprogram_sec1 {
  border: 2px solid rgb(128 128 128 / 15%);
  padding: 4.5rem;
}

.internshipprogram_sec2 {
  border: 2px solid rgb(128 128 128 / 15%);
  padding: 4.5rem;
}

.eduGrid_Item_Program {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.internshipprogram_sec1 p {
  font-family: "Poppins", sans-serif !important;
  font-weight: 500;
  line-height: 1.2;
  font-size: 16px;
  color: #3680f7;
}

.internshipprogram_sec1 span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  line-height: 1.2;
  font-size: 16px;
  color: #000;
}

.internshipprogram_sec2 p {
  font-family: "Poppins", sans-serif !important;
  font-weight: 500;
  line-height: 1.2;
  font-size: 16px;
  color: #3680f7;
}

.internshipprogram_sec2 span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  line-height: 1.2;
  font-size: 16px;
  color: #000;
}

.internshipprogram_sec1:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
  background-color: #f5f6f9;
}

.internshipprogram_sec2:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
  background-color: #f5f6f9;
}

@media screen and (max-width: 480px) {
  .eduIndProductGrid_Container {
    grid-template-columns: 1fr;
  }
}
