.govt-overiew-bg {
  background-image: url("../../assets/images/government-industries/overview-govt-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 75% 50%;
}

#smartGovernance {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
  background-image: url("../../assets/images/government-industries/smart-govt-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.smart-governance-sec {
  width: 100%;
  position: relative;
  background-image: linear-gradient(to right, #000000eb 5%, #2d2d3700 90%);
}

.smart-governance-sec .healthcare-technology-head-section p {
  color: #fff;
}

.smart-governance-sec .healthcare-technology-description {
  color: #fff;
}

@media screen and (max-width: 480px) {
  .govt-overiew-bg {
    display: none;
  }

  #smartGovernance {
    margin-bottom: 5rem;
  }

  .smart-governance-sec {
    width: 100%;
    position: relative;
    background-image: linear-gradient(to right, #000000eb 5%, #2d2d3700 90%);
  }
}
